<template>
	<div id="sort">
	<mt-header fixed title="">
		  <router-link to="/" slot="left">
		    <mt-button icon="back"></mt-button>
		    <mt-button></mt-button>
		  </router-link>
  
</mt-header>
		<div style="height:2rem;"></div>
		<div class="sortList" >
			<h3>:{{titled}}</h3>
			<ul>
				<li class="fix">
					<span class="fl"></span>
					<img :src="elsort.src" alt="" class="fl">
					<div class="fl">
						<h4>{{elsort.title}}</h4>
						<p><span class="colorF">{{elsort.moreR}}</span><span class="colorG">{{elsort.doCh}}</span></p>
					</div>
					<div class="fr"></div>
				</li>
			</ul>
		</div>
		<div class="maincon">
			<h4 class="theader"></h4>
			<ul>
				<li>
					<p>41477A3A</p>
					<div class="fix bottop">
						<div class="fl"><i class="icon iconfont icon-shijian"></i>
						<span>{{format(new Date())}}</span></div>
						<div class="fl" ><span style="margin-right:20px;">{{elsort.moreR}}</span><i @click='addZan(idn)' class="icon iconfont icon-zans"></i></div>
					</div>
				</li>
			</ul>
			    
		</div>
	</div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
	export default {
		data(){
			return{
				//tit:$route.parmas.thisT
				//elsort:[]
				date:'',
				idn:this.$route.query.id
			}
		},
		computed:{
			titled(){
				return (this.$store.state.sortList)[this.$route.query.id].title
			},
			elsort(){
				return (this.$store.state.sortList)[this.$route.query.id]
			},
			date(){
				return format(this.date)
			}
		},
		watch: {
      popupVisible2(val) {
        if (val) {
          setTimeout(() => {
            this.popupVisible2 = false;
          }, 2000);
        }
      }
    },
		mounted:function(){
			//this.elsort = JSON.parse(localStorage.getItem('el'))
			console.log(this.elsort.length)
		},
		methods:{
			goBack:function(){
				/*this.$router.go(-1);*/
				this.$router.push('/');
			},
			...mapActions([
				'addZan'
				]),
			add0:function(m){return m<10?'0'+m:m },
			format:function(shijianchuo){
				//shijianchuoparseInt
				var time = new Date(shijianchuo);
				var y = time.getFullYear();
				var m = time.getMonth()+1;
				var d = time.getDate();
				var h = time.getHours();
				var mm = time.getMinutes();
				var s = time.getSeconds();
				return y+'-'+(m<10?'0'+m:m)+'-'+(d<10?'0'+d:d);
				}
		}
	}
</script>
<style>
#sort{font-size: 0.7rem}
.maincon{padding:0 0.3rem 0.3rem 0.3rem;}
.theader,.maincon p{text-align: left;text-indent: 1em;line-height: 1.2rem;}
.theader{padding-bottom:0.3rem;}
.bottop div{height:2rem;line-height:2rem;width:50%;box-sizing:border-box;border:1px solid #ccc;
	margin-top:0.5rem;}
.bottop div i{vertical-align: middle;}

</style>


